import React from "react"
import { OptionsStyled } from "./Options.styled"
import { Switch } from "antd"

const Options = function (props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that
  let { word_hacks } = that.props
  /*
   * options
   */
  let use_word_hacks = !!that.props.suggestions_options.use_word_hacks
  /*
   * View
   */
  return (
    <OptionsStyled>
      <>
        {/*
         *
         * OPTIONS (always visible)
         *
         */}
        {!that.state.showOptions && (
          <div className="options">
            <div className="option">
              <span
                onClick={() => {
                  that.toggleOnlyAvailable()
                }}
              >
                <Switch className="Switch" size="small" checked={!that.props.only_available} />
                <span className="symbol hide-small">&nbsp;Include unavailable and expensive</span>
                <span className="symbol show-small">&nbsp;Show all results</span>
              </span>
            </div>
            {word_hacks.length > 0 && (
              <div className="option">
                <span
                  onClick={() => {
                    that.props.input_actions.RX__search_suggestions({
                      use_word_hacks: !use_word_hacks
                    })
                  }}
                >
                  <Switch className="Switch" size="small" checked={!!use_word_hacks} />
                  <span className="symbol">&nbsp;Include word hacks</span>
                </span>
              </div>
            )}
          </div>
        )}
      </>
    </OptionsStyled>
  )
}

export default Options
